import React from "react"

import Book from "./book"

const Books = ({ readed, reading }) => {
  const title = "book-list-container"
  return (
    <div key={title} className={title}>
      {reading.length > 0 && (
        <>
          <h3 className="text-shadow">Leyendo</h3>
          <ol className="book-list">
            {reading.map(b => (
              <Book book={b} />
            ))}
          </ol>
        </>
      )}
      <h3 className="text-shadow">Leídos</h3>
      <ol className="book-list">
        {readed.map(b => (
          <Book book={b} />
        ))}
      </ol>
    </div>
  )
}

export default Books
