import React, { useState } from "react"
import Select from "react-select"

import Layout from "./layout"
import SEO from "./seo"
import Books from "./books"
import BackButton from "./back-button"

const FILTER = {
  year: "year",
  title: "title",
  author: "author",
  genre: "genre",
}
const Library = ({ location, library }) => {
  const readingBooks = []
  const readedBooks = []

  const sortedBooks = library.books.sort(
    (a, b) =>
      new Date(b.readings[0].end_date) - new Date(a.readings[0].end_date)
  )

  sortedBooks.forEach(b => {
    if (b.readings[0].end_date) {
      readedBooks.push(b)
    } else {
      readingBooks.push(b)
    }
  })

  const lastReading = sortedBooks[sortedBooks.length - 1].readings[0]
  const lastYear = lastReading.end_date
    ? new Date(lastReading.end_date).getFullYear()
    : new Date(lastReading.start_date).getFullYear()
  const now = new Date()

  const selectYears = Array.from(
    { length: now.getFullYear() - lastYear + 1 },
    (_, i) => {
      const y = now.getFullYear() - i
      return { value: y, label: y }
    }
  )

  const title = library.name

  const [books, updateBooks] = useState(
    readedBooks.filter(b => {
      const lastReading = b.readings[0]
      const year = lastReading.end_date
        ? new Date(lastReading.end_date).getFullYear()
        : new Date(lastReading.start_date).getFullYear()
      return year === now.getFullYear()
    })
  )

  const [year, selectYear] = useState(selectYears[0])

  return (
    <Layout location={location} title={title}>
      <SEO title={title} />
      <h1>{title}</h1>

      <div className="select-year-container">
        <BackButton location={location} />
        <div className="select-year">
          {selectYears.length > 1 && (
            <Select
              value={year}
              onChange={y => {
                selectYear(y)
                updateBooks(
                  filterBooks({
                    books: readedBooks,
                    filters: { year: y.value + "" },
                  })
                )
              }}
              options={selectYears}
              theme={theme => ({
                ...theme,
                borderRadius: "var(--radius-1)",
                colors: {
                  ...theme.colors,
                  primary25: "var(--color-folders)",
                  primary: "var(--color-text)",
                },
              })}
            />
          )}
        </div>
      </div>

      <Books readed={books} reading={readingBooks} />
    </Layout>
  )
}

const filterBooks = ({ books, filters }) =>
  books.filter(b => {
    for (let k in filters) {
      if (k === FILTER.year) {
        for (let r of b.readings) {
          if (
            new Date(r.start_date).getFullYear() ===
            new Date(filters[k]).getFullYear()
          ) {
            return true
          }
        }
      } else if (k === FILTER.author) {
        if (filters[k].includes(b[k]?.name)) {
          return true
        }
      } else {
        return filters[k].includes(b[k])
      }
    }

    return false
  })

export default Library
